.pin-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f0f4f8;
    font-family: Arial, sans-serif;
  }
  
  h1 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .pin-input {
    padding: 10px;
    font-size: 1.5rem;
    text-align: center;
    border: 2px solid #0077cc;
    border-radius: 5px;
    width: 200px;
    margin-bottom: 10px;
  }
  
  .join-button {
    padding: 10px 20px;
    font-size: 1.2rem;
    color: white;
    background-color: #0077cc;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .join-button:hover {
    background-color: #005fa3;
  }
  
  .error-message {
    color: red;
    margin-top: 15px;
    font-weight: bold;
  }
  